.ms-header {
	box-shadow: 4px 2px 13px rgba(0, 0, 0, 0.1);
	background-color: #2a2a2a;
	height: 144px;
	&__nameWrp {
		padding-top: 65px;
	}
	&__name {
		color: #ffa200;
		font-family: 'Geometria';
		font-size: em(16);
		font-weight: 500;
		display: inline-block;
		&:last-child{
			margin-left: 100px;
		}
	}
}